import { ConvertToPluginActionCreators, ConvertToPluginActionPredicates, createActionHandlers } from '@ali/4ever-cangjie';

// undoAction
export const HISTORY_UNDO = Symbol('undoAction');
export const [undo, isUndo] =
  createActionHandlers(HISTORY_UNDO);

// redoAction
export const HISTORY_REDO = Symbol('redoAction');
export const [redo, isRedo] =
  createActionHandlers(HISTORY_REDO);

export const actions = {
  undo,
  redo,
};

export const isActions = {
  isUndo,
  isRedo,
};

/** @deprecated */
export const DEPRECATED_ACTIONS = {
  HISTORY_REDO,
  HISTORY_UNDO,
  undo,
  redo,
};

declare module '@ali/4ever-cangjie' {
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions> {}
  interface PluginActionPredicates extends ConvertToPluginActionPredicates<typeof isActions> {}
}
